import { Events } from "../../../utils/events"
import { Action, State } from "../types"
import EventEmitter from "eventemitter3"

const eventEmitter = window._correctorEvents || (window._correctorEvents = new EventEmitter())

export const paraphraseReducer = (state: State, action: Action): State => {

    switch (action.type) {
        case 'setText': {
            if (state && state.text === action.text) {
                return state
            }
            return {
                loading: true,
                text: action.text,
                offset: action.offset,
                length: action.length,
                uuid: action.uuid,
                segmentType: "Paraphrase",
                alternatives: undefined
            }
        }
        case 'setAlternatives': {
            if (!state) {
                throw ("Can't set alternatives with no state")
            }
            return {
                ...state,
                loading: false,
                alternatives: action.alternatives
            }
        }
        case 'selectAlternative': {
            if (!state) {
                throw ("Can't set alternatives with no state")
            }
            eventEmitter.emit<Events>('select-alternative', {
                offset: state.offset,
                length: state.length,
                text: action.alternative
            })
            return undefined
        }
    }
}