import { Segment, SizeChange } from "../config/types"

export abstract class SegmentRepository<T extends Segment>{
    private sentences : T[] = []
    protected enabled = false
    private ignoredUuids: Set<string> = new Set() 

    public enable() {
        this.enabled = true
    }

    public disable() {
        this.enabled = false
    }

    public isEnabled() {
        return this.enabled
    }

    public setSegments(matches: T[]) {
        this.sentences = matches.filter(e => !this.ignoredUuids.has(e.uuid)).map(e => structuredClone(e))
    }

    public reset() {
        this.sentences = []
    }

    public hasMatches() {
        return this.sentences.length > 0
    }

    public getAll(): T[] {
        return this.enabled ? this.sentences || [] : []
    }

    public ignore(uuid: string) {
        this.delete(uuid)
        this.ignoredUuids.add(uuid)
    }

    public delete(applied: string) {
        this.sentences = this.sentences.filter(m => m.uuid !== applied)
    }

    public update(applied: string, length: number) {
        let startingOffset = -1
        let difference = 0
        let found = false

        for(const match of this.sentences) {
            if(match.uuid === applied) {
                found = true
                startingOffset = match.offset
                difference = length - match.length
                break
            }
        }

        if (found) {
            this.processChanges({ position: startingOffset + 1, difference})
            this.sentences = this.sentences.filter(m => m.uuid !== applied)
        }
    }

    public processChanges({ position, difference } : SizeChange) {
        this.sentences?.filter(m => m.offset >= position)
            ?.forEach(m => m.offset += difference)
    }
    

    public get(uuid: string) {
        return this.sentences.find(m => m.uuid === uuid)
    }
    
}