import { createContext, useCallback, useEffect, useReducer } from "react"
import type { Paraphrasable, State } from '../types'
import { paraphraseReducer } from "./reducer";
import { sentenceSplitter } from "../sentenceSplitter";
import { ParaphraseClient } from "../client";
import { SentenceRepository } from "../SentenceRepository";


export type ParaphraseContext = { 
    state: State; 
    setParaphraseText: (current: Paraphrasable) => void; 
    selectAlternative: (text: string) => void
    getSentences: (text: string) => Promise<Paraphrasable[]>
    sentenceRepository: SentenceRepository
} | undefined

const sentenceRepository = new SentenceRepository()

export const Context = createContext<ParaphraseContext>(undefined)

const getSentences = async (text : string) : Promise<Paraphrasable[]> => {
    
    const response = sentenceSplitter(text)

    if (response.success) {
        sentenceRepository.setSegments(response.sentences)

        return response.sentences
    }

    // TODO: què passa si hi ha error?
    return []
}

export const ParaphraseProvider = ({ children }: { children: React.ReactNode }) => {

    const [state, dispatch] = useReducer(paraphraseReducer, undefined)

    useEffect(() => {
        async function getAlternatives(text : string) {
            const alternatives = await ParaphraseClient.getParaphraseResults(text)
            dispatch({
                type: "setAlternatives",
                alternatives
            })
        }

        if (state?.loading && state?.text) {
            getAlternatives(state.text)
        }

    }, [state?.loading, state?.text])

    const setParaphraseText = useCallback((text: Paraphrasable) => {
        dispatch({
            type: 'setText', ...text
        })
    }, [])

    const selectAlternative = useCallback((alternative : string) => {
        dispatch({
            type: 'selectAlternative', alternative
        })
    }, [])

    return (
        <Context.Provider value={{ state, setParaphraseText, selectAlternative, getSentences, sentenceRepository }}>
            {children}
        </Context.Provider>
    )
}

