import { UUID } from "../../utils/uuid"
import { Paraphrasable, SentenceSplitResult } from "./types"
import { SrxSegmenter } from "./SrxSegmenter"
import { segmentSrxData } from "./segmentSrxData"


export const sentenceSplitter = (text: string): SentenceSplitResult => {

    var segmenter = new SrxSegmenter(text, segmentSrxData)
    const [sentences, whitespaces] = segmenter.extract()
    let offset = 0

    const paraphrasables = sentences.map((sentence, index) => {
        let length = sentence.length
        let whitespace = whitespaces[index];
        offset += whitespace.length
        const paraphrasable : Paraphrasable = { text: sentence, offset: offset, length: length, uuid: UUID.generate(), segmentType: "Paraphrase"}
        offset += length
        return paraphrasable
    }).filter(paraphrasable => paraphrasable.text.length > 0)

    return {
        success: true,
        sentences: paraphrasables
    }
}
