import * as xpath from 'xpath';
import * as dom from 'xmldom';

interface Rule {
    breaks: Array<[string, string?]>;
    non_breaks: Array<[string, string?]>;
}

interface RulesDict {
    [key: string]: Rule;
}

export class SrxSegmenter {
    sourceText: string;
    nonBreaks: Array<[string, string?]>;
    breaks: Array<[string, string?]>;
        
    constructor(sourceText: string, SrxData: string) {

        const rules = this.parse(SrxData);
        const rule = rules["Catalan"];

        this.sourceText = sourceText;
        this.nonBreaks = rule.non_breaks;
        this.breaks = rule.breaks;
    }    
    
    private transformRegularExpression(pattern: string) {
        let output = pattern
        output = output.replace("(?iu)", "")
        return output
    }

    private _getBreakPoints(regexes: Array<[string, string?]>) {
        const s: Set<number> = new Set();
        for (const [before, after] of regexes) {
            const srcPattern = `(${before})(${after || ''})`;
            const pattern = this.transformRegularExpression(srcPattern)
            let flags = "gu"
            if (srcPattern.includes("?iu")) {
                flags += "i"
            }
            
            let compiled = new RegExp(pattern, flags);
            let match;
            while ((match = compiled.exec(this.sourceText)) !== null) {
                let index = match.index + match[1].length
                s.add(index);
            }
        }
        return s;
    }


    private getNonBreakPoints() {
        return this._getBreakPoints(this.nonBreaks);
    }

    private getBreakPoints() {
        return this._getBreakPoints(this.breaks);
    }

    private partition(str: string, separator: string): [string, string, string] {
        const [head, tail] = str.split(separator, 2);
        return [head || '', separator, tail || ''];
    }
    
    public extract() {
        // Return segments and whitespaces.
        const non_break_points: number[] = Array.from(this.getNonBreakPoints());
        const candidate_break_points: number[] = Array.from(this.getBreakPoints());

        const break_point: number[] = candidate_break_points
            .filter(bp => !non_break_points.includes(bp))
            .sort((a, b) => a - b);

        const source_text: string = this.sourceText;
        const segments: string[] = [];
        const whitespaces: string[] = [];
        let previous_foot: string = "";

        for (let i = 0; i < break_point.length + 1; i++) {
            const start = (i === 0) ? 0 : break_point[i - 1];
            const end = (i === break_point.length) ? source_text.length : break_point[i];
            const segment_with_space = source_text.slice(start, end);
            const candidate_segment = segment_with_space.trim();

            if (!candidate_segment) {
                previous_foot += segment_with_space;
                continue;
            }

            const [head, segment, foot] = this.partition(segment_with_space, candidate_segment);

            segments.push(segment);
            whitespaces.push(`${previous_foot}${head}`);
            previous_foot = foot;
        }
        whitespaces.push(previous_foot);

        return [segments, whitespaces];
        }
    
    private parse(xmlData: string): RulesDict {
        const doc = new dom.DOMParser().parseFromString(xmlData);
        const select = xpath.useNamespaces({"ns": "http://www.lisa.org/srx20"});
        const rules: RulesDict = {};

        var languageRules = select("//ns:languagerule", doc);
        
        for (const languageRule of languageRules as Node[]) {

            const element = languageRule as Element;
            const ruleName = element.attributes.getNamedItem('languagerulename')?.value; 

            if (!ruleName)
                continue
                            
            const currentRule: Rule = {
                breaks: [],
                non_breaks: []
            };

            const ruleElements = select("ns:rule", languageRule);
            for (const ruleElement of ruleElements as Element[]) {
                const isBreak = ruleElement.getAttribute('break') === 'yes';
                const ruleHolder = isBreak ? currentRule.breaks : currentRule.non_breaks;

                const beforeBreakElements = select("ns:beforebreak", ruleElement) as Element[];
                const beforeBreak = beforeBreakElements &&  beforeBreakElements[0]?.textContent || '';

                const afterBreakElements = select("ns:afterbreak", ruleElement) as Element[];
                const afterBreak = afterBreakElements &&  afterBreakElements[0]?.textContent || '';

                ruleHolder.push([beforeBreak, afterBreak]);
            }
            
            rules[ruleName] = currentRule;
        }

        return rules;
    }
}
